@import "~@/scss/global_var.scss";





























































































































































.SignIn .title{
    color: $color-title;
}

.SignIn.darkMode{
    .theme--light.v-card{
        border: unset;
        border-radius: 15px;
        padding-top: 0px;
        padding-bottom: 15px;
        background: linear-gradient(to bottom right, #7d7a8280, #38343e );

        .title{
            color: white;
            @include vm-drop-shadow-1(0px, 0px, 5px, rgba(197, 195, 195, 0.5));
        }

        .content{
            margin: 0 auto;
            display: flex;
            flex-direction: column;
            max-width: 400px;
            background: linear-gradient(to bottom right, #ffffff 75%, #dadada );;
            padding: 20px;
            padding-top: 30px;
            padding-bottom: 10px;
            border-radius: 15px;
            @include vm-drop-shadow-1(0px, 0px, 10px, rgba(197, 195, 195, 0.5));
        }

        .card-action{
            padding-top: 10px !important;
            margin: 0 auto;
            max-width: 400px;
        }
    }        
}
