@import "~@/scss/global_var.scss";

























































































































.Home{
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;

    width: 100%;
    height: 100%;
    
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    
    .bg{
        z-index: 0;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;

        background: url("~@/assets/img/forest/forest2.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        filter: blur(2px) opacity(0.8) brightness(80%);
    }

    .page-fipper{
        z-index: 2;
    }

    .default-screen{
        @include vm-transition(all, 0.5s);

        height: 500px;
        &.with-buttons{
            height: 600px;
        }
    }

    .content{
        position: relative;
        z-index: 1;
        width: 90%;
        height: 95%;
        border-radius: 50px;
        @include vm-drop-shadow-1(0px, 0px, 15px, rgba(197, 195, 195, 0.5));

        $hover-width: 15px;
        $color-hover: rgba(197, 195, 195, 0.5);
        box-shadow: 
            0px 0px $hover-width $color-hover;

        background: url("~@/assets/img/forest/forest2.jpg");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;

        @include vm-transition(all, 0.5s);
    }

    &.md, &.sm, &.xs{
        .content{
            width: 95%;
            height: 95%;
        }
    }

    &.blur-background{
        .content{
            width: 100%;
            height: 100%;
            border-radius: 0px;

            .container{
                backdrop-filter: blur(10px);
            }
        }
    }
}

.Home{
    .buttons{
        @media (max-width: 751px) { 
            display: flex;
            flex-direction: column-reverse;
            justify-content: center;
            align-items: center;
        }
    }
    
    .buttons .button{
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        width: 250px;
        color: #daf6ff;
        text-shadow: 0 0 20px rgba(10, 175, 230, 1),  0 0 20px rgba(10, 175, 230, 0);
        border-bottom: 5px solid #daf6ff;
        box-shadow: 0 6px 8px -6px rgba(10, 175, 230, 1), 0 8px 6px -6px rgba(10, 175, 230, 0);
    }

    .github-icon{
        position: absolute;
        right: 20px;
        bottom: 20px;
        .button{
            width: 50px;
            border-bottom: unset;
            box-shadow: unset;
            color: #daf6ff !important;
            text-shadow: 0 0 20px rgba(10, 175, 230, 1),  0 0 20px rgba(10, 175, 230, 0);                
       }
    }

    &.xs .buttons .button{
        width: 200px;
    }
    &.xs .buttons.github-icon .button{
        width: 50px;
    }
}
