@import "~@/scss/global_var.scss";











































































































































































.flex-container{
    display: flex;
    flex-direction: row;
    // width: 150vw;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}

.flex-container .flipper{
    z-index: 100;
    flex: 1;
    // width: 250px;
    height: 150vh;
    margin-top: -150px;
    // margin-left: 10px;
    // margin-right: 10px;

    // background-color: #1c2f46;
    // background-image: map-get($imgs-heavy, grey_wash_wall);
    background-image: map-get($imgs-heavy, always_grey);
    // background-image: map-get($imgs-heavy, footer_lodyas);
    // background-image: map-get($imgs-heavy, dark_wood);
    background-repeat: repeat;

    -ms-transform: rotate(8deg); /* IE 9 */
    transform: rotate(8deg);

    // border: 5px solid #a1b7bd;

    &:first-child{
        margin-left: -120px;
    }

    &:last-child{
        margin-right: -80px;
    }

}
